"use client";

import React, { useState, useEffect } from "react";
import Link from "next/link";
import { Button } from "~/components/ui/button";
import QuickClipsLogo from "~/components/logo";
import { ScrollReveal } from "~/components/scroll-reveal";
import { X, Menu } from "lucide-react";

const MobileNavHeader = () => {
  const [isMobileMenuOpen, setIsMobileMenuOpen] = useState(false);

  const toggleMobileMenu = () => {
    setIsMobileMenuOpen(!isMobileMenuOpen);
  };

  const closeMobileMenu = () => {
    setIsMobileMenuOpen(false);
  };

  // Prevent body scroll when mobile menu is open
  useEffect(() => {
    if (isMobileMenuOpen) {
      document.body.style.overflow = 'hidden';
    } else {
      document.body.style.overflow = 'unset';
    }
    
    return () => {
      document.body.style.overflow = 'unset';
    };
  }, [isMobileMenuOpen]);

  return (
    <header className="sticky top-0 z-50 border-b border-border/40 glass-card shadow-glow">
      <div className="container mx-auto flex h-16 items-center justify-between px-4">
        {/* Logo - responsive */}
        <ScrollReveal direction="left">
          <div className="flex items-center">
            {/* Mobile: Show only icon */}
            <div className="md:hidden">
              <QuickClipsLogo variant="icon" />
            </div>
            {/* Desktop: Show full logo */}
            <div className="hidden md:block">
              <QuickClipsLogo variant="full" />
            </div>
          </div>
        </ScrollReveal>

        {/* Desktop Navigation */}
        <nav className="hidden md:flex items-center space-x-6">
          <ScrollReveal direction="up" delay={100}>
            <Link href="#features" className="text-sm font-medium text-muted-foreground hover:text-primary transition-colors duration-300">
              Features
            </Link>
          </ScrollReveal>
          <ScrollReveal direction="up" delay={150}>
            <Link href="#pricing" className="text-sm font-medium text-muted-foreground hover:text-primary transition-colors duration-300">
              Pricing
            </Link>
          </ScrollReveal>
          <ScrollReveal direction="up" delay={200}>
            <Link href="/about" className="text-sm font-medium text-muted-foreground hover:text-primary transition-colors duration-300">
              About
            </Link>
          </ScrollReveal>
          <ScrollReveal direction="up" delay={250}>
            <Link href="/affiliate" className="text-sm font-medium text-muted-foreground hover:text-primary transition-colors duration-300">
              Affiliate
            </Link>
          </ScrollReveal>
        </nav>

        {/* Desktop Buttons */}
        <div className="hidden md:flex items-center gap-3">
          <ScrollReveal direction="right" delay={100}>
            <Button asChild variant="ghost" className="secondary-button">
              <Link href="/login">Sign In</Link>
            </Button>
          </ScrollReveal>
          <ScrollReveal direction="right" delay={200}>
            <Button asChild className="primary-button">
              <Link href="/signup">Get Started</Link>
            </Button>
          </ScrollReveal>
        </div>

        {/* Mobile Hamburger Button */}
        <div className="md:hidden">
          <Button
            variant="ghost"
            size="sm"
            onClick={toggleMobileMenu}
            className="p-2 text-foreground hover:text-primary transition-colors"
          >
            {isMobileMenuOpen ? (
              <X className="h-6 w-6" />
            ) : (
              <Menu className="h-6 w-6" />
            )}
          </Button>
        </div>

        {/* Mobile Menu Overlay */}
        {isMobileMenuOpen && (
          <div 
            className="fixed inset-0 bg-black/50 z-40 md:hidden"
            onClick={closeMobileMenu}
          />
        )}

        {/* Mobile Menu Sliding Panel */}
        {isMobileMenuOpen && (
          <div className="fixed top-0 right-0 h-full w-80 bg-card/95 backdrop-blur-xl border-l border-border shadow-2xl transform transition-transform duration-300 ease-in-out z-50 md:hidden">
            <div className="flex flex-col h-full">
              {/* Mobile menu header */}
              <div className="flex items-center justify-between p-6 border-b border-border/40">
                <QuickClipsLogo variant="full" />
                <Button
                  variant="ghost"
                  size="sm"
                  onClick={closeMobileMenu}
                  className="p-2 hover:bg-primary/10"
                >
                  <X className="h-6 w-6" />
                </Button>
              </div>

              {/* Mobile Navigation Links */}
              <nav className="flex-1 px-6 py-8 space-y-6">
                <Link 
                  href="#features" 
                  className="block text-lg font-medium text-foreground hover:text-primary transition-colors py-3 border-b border-border/20"
                  onClick={closeMobileMenu}
                >
                  Features
                </Link>
                <Link 
                  href="#pricing" 
                  className="block text-lg font-medium text-foreground hover:text-primary transition-colors py-3 border-b border-border/20"
                  onClick={closeMobileMenu}
                >
                  Pricing
                </Link>
                <Link 
                  href="/about" 
                  className="block text-lg font-medium text-foreground hover:text-primary transition-colors py-3 border-b border-border/20"
                  onClick={closeMobileMenu}
                >
                  About
                </Link>
                <Link 
                  href="/affiliate" 
                  className="block text-lg font-medium text-foreground hover:text-primary transition-colors py-3 border-b border-border/20"
                  onClick={closeMobileMenu}
                >
                  Affiliate
                </Link>
                <Link 
                  href="/contact" 
                  className="block text-lg font-medium text-foreground hover:text-primary transition-colors py-3 border-b border-border/20"
                  onClick={closeMobileMenu}
                >
                  Contact
                </Link>
              </nav>

              {/* Mobile Buttons */}
              <div className="px-6 pb-8 space-y-4">
                <Button asChild className="w-full secondary-button h-12" onClick={closeMobileMenu}>
                  <Link href="/login">Sign In</Link>
                </Button>
                <Button asChild className="w-full primary-button h-12" onClick={closeMobileMenu}>
                  <Link href="/signup">Get Started</Link>
                </Button>
              </div>
            </div>
          </div>
        )}
      </div>
    </header>
  );
};

export default MobileNavHeader;